import React, { useState, useEffect } from 'react';
import { StyleSheet, ScrollView, View, Content, Button, Alert, TouchableWithoutFeedback } from 'react-native';
import { List, ListItem, Body, Left, Right, Text, Switch, Icon } from 'native-base';

import { db } from './firebase/config';
import './Extensions';
import color from 'color';

const TodayTasksScreen = (props) => {
  const [todoList, setTodoList] = useState([]);
  const [selectedTasks, setSelectedTasks] = useState([]);  
  const [doneTasks, setDoneTasks] = useState([]);
  const [dateStr, setDate] = useState(new Date().format());

  useEffect(()=>{
    let selectTasks = [];
    let doneTasks = [];
    db.collection('selectlist' + props.user).doc(dateStr).get().then((selectDoc) => {                
      if (selectDoc.exists && selectDoc.data().selectedTasks){      
        selectDoc.data().selectedTasks.forEach(item => {
          selectTasks.push(item);
        });
        setSelectedTasks(selectTasks);
      }      
    });

    db.collection('donelist' + props.user).doc(dateStr).get().then((doneDoc) => {                
      if (doneDoc.exists && doneDoc.data().doneTasks){      
        doneDoc.data().doneTasks.forEach(item => {
          doneTasks.push(item);
        });
        setDoneTasks(doneTasks);
      }
    })

    db.collection('todolist').doc('values').get().then((userDoc) =>{      
      if (userDoc.exists && userDoc.data().allTodo){
        let todoList = [];
        userDoc.data().allTodo.forEach(v => {
          v.selected = selectTasks.includes(v.ID);
          v.done = doneTasks.includes(v.ID);
          todoList.push(v);
        })
        setTodoList(sortTodo(todoList));
      } else {
        console.log("No doc values found");
        db.collection('todolist').doc('values').set({allTodo: [{   "ID": 1,   "Task": "Làm việc nhà: Dọn phòng, dọn nhà vệ sinh, dọn sân thượng, rửa bát",   "Cat": "Việc nhà",   "Time": 30,   "FreqPerWeek": 3 }, {   "ID": 2,   "Task": "Làm Doulingo",   "Cat": "T.A",   "Time": 20,   "FreqPerWeek": 7 }, {   "ID": 3,   "Task": "Làm Scratch",   "Cat": "ICT",   "Time": 90,   "FreqPerWeek": 2 }, {   "ID": 4,   "Task": "Học tầng cầu",   "Cat": "Thể thao",   "Time": 30,   "FreqPerWeek": 2 }, {   "ID": 5,   "Task": "Vẽ tranh, tô màu",   "Cat": "Sáng tạo",   "Time": 45,   "FreqPerWeek": 2 }, {   "ID": 6,   "Task": "Sáng tác truyện tranh",   "Cat": "Sáng tạo",   "Time": 60,   "FreqPerWeek": 2 }, {   "ID": 7,   "Task": "Đánh đàn",   "Cat": "Nghệ thuật",   "Time": 15,   "FreqPerWeek": 3 }, {   "ID": 8,   "Task": "Đu xà",   "Cat": "Thể thao",   "Time": 5,   "FreqPerWeek": 7 }, {   "ID": 9,   "Task": "Thuyết trình về nhân vật",   "Cat": "K.N",   "Time": 45,   "FreqPerWeek": 2 }, {   "ID": 10,   "Task": "Kể chuyện, đọc chuyện",   "Cat": "K.N",   "Time": 20,   "FreqPerWeek": 2 }, {   "ID": 11,   "Task": "Đọc chuyện tiếng anh",   "Cat": "T.A",   "Time": 30,   "FreqPerWeek": 2 }, {   "ID": 12,   "Task": "Làm bài tập toán",   "Cat": "Kiến thức",   "Time": 45,   "FreqPerWeek": 3 }, {   "ID": 13,   "Task": "Làm bài tập Tiếng việt",   "Cat": "Kiến thức",   "Time": 45,   "FreqPerWeek": 3 }, {   "ID": 14,   "Task": "Làm thơ",   "Cat": "Sáng tạo",   "Time": 30,   "FreqPerWeek": 2 }, {   "ID": 15,   "Task": "Làm bài tập Apax",   "Cat": "T.A",   "Time": 90,   "FreqPerWeek": 2 }, {   "ID": 16,   "Task": "Đóng kịch ngắn, làm video Youtube",   "Cat": "Sáng tạo",   "Time": 60,   "FreqPerWeek": 1 }, {   "ID": 17,   "Task": "Ghép lego",   "Cat": "Sáng tạo",   "Time": 30,   "FreqPerWeek": 2 }, {   "ID": 18,   "Task": "Học cờ vua",   "Cat": "Thể thao",   "Time": 60,   "FreqPerWeek": 3 }, {   "ID": 19,   "Task": "Giải toán Kangaroo",   "Cat": "Kiến thức",   "Time": 60,   "FreqPerWeek": 3 }, {   "ID": 20,   "Task": "Nhặt rau, chuẩn bị đồ ăn",   "Cat": "Việc nhà",   "Time": 30,   "FreqPerWeek": 3 }, {   "ID": 21,   "Task": "Viết câu T.A",   "Cat": "T.A",   "Time": 15,   "FreqPerWeek": 7 }, {   "ID": 22,   "Task": "Viết lời thoại, dịch phim hoạt hình Peppa Pig",   "Cat": "T.A",   "Time": 30,   "FreqPerWeek": 1 }, {   "ID": 23,   "Task": "Hát bài hát T.A",   "Cat": "T.A",   "Time": 30,   "FreqPerWeek": 1 }, {   "ID": 24,   "Task": "Tập tâng bóng 20 cái, dẫn chuyền bóng 2 chân (100 cái)",   "Cat": "Thể thao",   "Time": 30,   "FreqPerWeek": 1 }, {   "ID": 25,   "Task": "Múa, uốn dẻo",   "Cat": "Thể thao",   "Time": 10,   "FreqPerWeek": 3 }, {   "ID": 26,   "Task": "Workout 5p",   "Cat": "Thể thao",   "Time": 5,   "FreqPerWeek": 3 }, {   "ID": 27,   "Task": "Bắn cung (2 lần -> 100 điểm)",   "Cat": "Thể thao",   "Time": 30,   "FreqPerWeek": 1 }]});
      }
    });

  },[dateStr]);

  const sortTodo = (list) => {
    return list.sort((x,y)=> {
      if (x.selected == y.selected){
        return x.ID > y.ID;
      }
      else {
        return y.selected - x.selected;
      }
    })
  }

  const onSelectItem = (id) => {    
    const todoTemp = [...todoList];
    todoTemp.find(x => x.ID == id).selected = !todoTemp.find(x => x.ID == id).selected;        
    setTodoList(sortTodo(todoTemp));
    let selectedTemp = [];
    if (selectedTasks.includes(id)){
      // User don't want the task, remove it
      selectedTemp = selectedTasks.filter(x => x !== id);
      // remove it from done list too      
      if (doneTasks.includes(id)){        
        setDoneTasks(doneTasks.filter(x => x !== id));
      }
    }
    else {
      selectedTemp = [...selectedTasks];
      selectedTemp.push(id);
    }    
    setSelectedTasks(selectedTemp);    
  };

  const SaveSelectList = () => {
    db.collection('selectlist' + props.user).doc(dateStr).set({selectedTasks: selectedTasks, date: new Date()});
    // setTimeout(() => {
    //   Alert.alert('Xác nhận','Lưu danh sách việc đã chọn',[{
    //     text: 'Không',
    //     style: "cancel"
    //   },{
    //     text: 'OK',
    //     onPress: () => {
    //       db.collection('selectlist' + props.user).doc(dateStr).set({selectedTasks: selectedTasks, date: new Date()});
    //     },
    //     style: 'ok'
    //   }],{ cancelable: false });
    // },10);
  }
  
  const onDoneItem = (id) => {
    if (!selectedTasks.includes(id)){
      Alert.alert('This job has not selected!');
    }
    let doneTemp = [];
    if (doneTasks.includes(id)){
      doneTemp = doneTasks.filter(x => x !== id);
    }
    else {
      doneTemp = [...doneTasks];
      doneTemp.push(id);
    }
    setDoneTasks(doneTemp);
    db.collection('donelist' + props.user).doc(dateStr).set({doneTasks: doneTemp, date: new Date()});
  };  

  const SelectDay = (val) =>{
    const currentDate = dateStr.toDate();
    currentDate.setDate(currentDate.getDate() + val);    
    setDate(currentDate.format());
  }

  return (
     <ScrollView>
        <View style={{flexDirection: 'column'}}>
          <View style={{flexDirection: 'row', justifyContent: 'space-between', marginTop: 20, marginBottom: 20 }}>
             <Button style={{size: ''}} title="<<" onPress={()=> SelectDay(-1)}/>
             <Button style={{size: ''}} title="Lưu việc đã chọn" onPress={()=> SaveSelectList()}></Button>
             <Button style={{size: ''}} title=">>" onPress={()=> SelectDay(1)}/>
          </View>
          <Text style={styles.result} > Ngày {dateStr}, {props.user} đã chọn {selectedTasks.length} việc. {"\r\n"} Đã hoàn thành: {doneTasks.length} việc</Text>
        </View>
        <Text style={{ fontSize: 18, fontWeight: 'bold'}}>Danh sách công việc</Text>
      <List>
      {        
        todoList.map((l, i) => (          
         <ListItem key={i} bottomDivider style={{ backgroundColor: l.selected? '#cde1f9': '#fcfcfc'}}>
             <Body>
               <View>
                 <TouchableWithoutFeedback onPress={() => onSelectItem(l.ID)}>
                   <View>
                      <Text style={styles.titleText}>{l.ID}. {l.Task}</Text>
                      <Text style={styles.baseText}>Mục: {l.Cat} </Text>
                      <Text style={styles.baseText}>Lần chọn theo tuần: {l.FreqPerWeek} </Text>
                   </View>
                 </TouchableWithoutFeedback>
               </View>
             </Body>
             <Right>               
              {/* <Button title="Detail" onPress={() => Alert.alert('Simple Button pressed')} >                            
              </Button> */}
               { selectedTasks.includes(l.ID) && (
                  <Switch value={doneTasks.includes(l.ID)} onValueChange={(e)=> { onDoneItem(l.ID)}} onTouchEnd={(e) => {
                    e.stopPropagation();
                  }}/>
                )
              }
             </Right>
         </ListItem>
        ))
      }
      </List>
    </ScrollView>
  )
}

const styles = StyleSheet.create({
  baseText: {
    fontSize: 11,
  },
  titleText: {
    fontSize: 15,
    fontWeight: 'bold'   
  },
  result: {
    marginLeft: '50px',
    marginBottom: 20,
    fontSize: 18,
    fontWeight: 'bold',
    color: "red"
  }
});


export default TodayTasksScreen;